.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  padding: 14px 0 28px;

  @include vp-1199 {
    padding: 14px 0 28px;
  }

  @media screen and (max-width: 767px) {
    padding: 4px 0 25px;
    width: 100%;
  }

  label {
    display: flex;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }

    input::placeholder {
      color: $color-default-white;
      font-size: 14px;
      line-height: 16.41;
      font-weight: 400;
    }

    input,
    textarea {
      flex-grow: 1;
      min-height: 31px;
      margin-left: auto;
      padding: 5px 10px 8px 0;
      border: 1px solid transparent;
      border-bottom: 1px solid $color-light-slate-grey;
      outline: none;
      transition: border 0.3s ease;
      background-color: transparent;
      color: $color-default-white;

      @include vp-1199 {
        padding-bottom: 6px;
      }

      @media screen and (max-width: 767px) {
        max-width: unset;
        width: 100%;
      }

      &:hover {
        opacity: 0.7;
      }

      &:focus {
        opacity: 0.7;
      }
    }
  }

  &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: 35px;
    color: #ff1553;
    opacity: 0;
    transition: opacity 0.3s ease;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  }

  &.is-invalid {
    input,
    textarea {
      border: 1px solid #ff1553;

      &:focus,
      &:hover {
        border: 1px solid rgba(#ff1553, 0.5);
      }
    }
  }

  &.is-valid {
    input {
      border: 1px solid #22b259;

      &:focus,
      &:hover {
        border: 1px solid rgba(#22b259, 0.5);
      }
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: 0;

    &.is-invalid {
      color: #ff0000;
    }

    &.is-valid {
      color: #15ab07;
    }
  }
}
