*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 24px;
  font-family: $ff-text;
  color: $color-sapphire;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  //      
  text-rendering: optimizeLegibility;
  //       
  // https://transfonter.org/ -   https://prnt.sc/12rnt6g   
}

body {
  width: 100%;
  height: 100%;
  background-color: $color-default-white;
}

h1 {
  margin: 0;
  font-family: $ff-title;
  color: $color-default-white;
  font-size: 92px;
  line-height: 110.4px;
  font-weight: 600;
  letter-spacing: 4.7px;
  text-transform: uppercase;
  -webkit-text-stroke: 2px $color-default-white;

  @include vp-1199 {
    font-size: 72px;
    line-height: 86.4px;
    letter-spacing: 3.6px;
    text-transform: none;
  }
}

h2 {
  margin: 0;
  font-family: $ff-title;
  font-size: 54px;
  line-height: 57.6px;
  font-weight: 600;
  letter-spacing: 1.62px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 0.96px;
  }
}

h3 {
  margin: 0;
  font-family: $ff-text;
  font-size: 26px;
  line-height: 28.64px;
  font-weight: 700;

  @include vp-1199 {
    font-size: 26px;
    line-height: 28.64px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 24.23px;
  }
}

h4 {
  margin: 0;
  color: $color-default-white;
  font-family: $ff-text;
  font-size: 18px;
  line-height: 27px;
  font-weight: 700;
}

a {
  color: $color-default-black;
  text-decoration: none;
  transition: opacity $trans-default;

  &:hover {
    opacity: 0.7;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
//         \ -
//          
// rgba  ,    hex  
//      box-shadow    -
//     `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;
  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
